html, body {
    width: 100%;
    margin: 0;
    padding: 0;
    font-size: 18px;
}


canvas {
    width: 540px;
    height: 540px;
    max-width: 100%;
    image-rendering: -moz-crisp-edges;
    image-rendering: -webkit-crisp-edges;
    image-rendering: pixelated;
    image-rendering: crisp-edges;
}

.studio {
    width: 100%;
}

.paper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 540px;
    height: 540px;
    max-width: 100%;
    image-rendering: -moz-crisp-edges;
    image-rendering: -webkit-crisp-edges;
    image-rendering: pixelated;
    image-rendering: crisp-edges;
}

fieldset {
    border: 1px solid #eee;
    padding: 1.5em;
    margin: 0;
    max-width: 100%;
    width: 540px;
    box-sizing: border-box;
}

legend {
    padding: 0;
    text-transform: uppercase;
    font-family: monospace;
    font-weight: bold;
    font-size: 0.618em;
}

label {
    font-family: monospace;
    text-transform: uppercase;
    margin-bottom: 2em;
    display: block;
    font-size: 0.618em;
}

label input {
    display: block;
    width: 100%;
}

.container {
    display: flex;
    flex-wrap: wrap;
}

.column {
    width: 50%;
    max-width: 540px;
    margin-right: 27px;
}

@media (max-width: 1080px) {
    .column {
        width: 100%;
        margin-right: 0;
        margin-bottom: 27px;
    }
    .column:last-child {
        margin-bottom: 0;
    }
}